.header{
  background: linear-gradient(90deg, #672280 1.18%, #A626D3 100%);
  height: 65px;
  display: flex;
  justify-content: space-between;
  padding: 20px;
}
.header-text{
  font-size: 12px;
  font-weight: 500;
  color: white;
}
.troll-face{
  width: 31px;
  height: 31px;
  margin-top: 16px;
  margin-right: 6px;
}
.meme-text{
  font-size: 21px;
  font-weight: 700;
  color: white;
  letter-spacing: -0.1em;
  margin-right: auto;
}
.meme-parent{
  padding: 36px;
  text-align: center;
}
.meme{
  display: grid;
  grid-template: 40px 40px / 1fr 1fr;
  gap: 17px;
}
.meme-input{
  border: 1px solid #D5D4D8;
  box-sizing: border-box;
  border-radius: 5px;
  text-indent: 5px;
}
.meme-button{
  grid-column: 1 / -1;
  background: linear-gradient(90.41deg, #711F8D 1.14%, #A818DA 100%);
  border-radius: 5px;
  border: none;
  color: white;
  font-size: 16px;
  cursor: pointer;
}
.meme-content{
  position: relative;
}
.meme-ph{
  max-width: 100%;
  border-radius: 3px;
  margin-top: 10px;
}
.meme-head {
  position: absolute;
  width: 80%;
  text-align: center;
  left: 50%;
  transform: translateX(-50%);
  margin: 15px 0;
  padding: 0 5px;
  font-family: impact, sans-serif;
  font-size: 2em;
  text-transform: uppercase;
  color: white;
  letter-spacing: 1px;
  text-shadow:
      2px 2px 0 #000,
      -2px -2px 0 #000,
      2px -2px 0 #000,
      -2px 2px 0 #000,
      0 2px 0 #000,
      2px 0 0 #000,
      0 -2px 0 #000,
      -2px 0 0 #000,
      2px 2px 5px #000;
}
.bottom {
  bottom: 0;
}

.top {
  top: 0;
}
@media screen and (max-width: 600px){
  .meme{
    grid-template: none;
  }
  .meme-button, .meme-input{
    padding: 7px;
  }
}